<template>
	<view>
		<view class="message">
			<view class="header">
				<view class="desc">{{ sendTime }}</view>
				<view class="opt" @tap="deleteMsg()">删除</view>
			</view>
			<view class="content">{{ msg }}</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: null,
				readFlag: null,
				refId: null,
				sendTime: '',
				senderName: '',
				msg: ''
			}
		},
		onShow() {
			let that = this;
			that.ajax(that.url.searchMessageById, "POST", {"id": that.id}, function(resp){
				let result = resp.data.result;
				that.sendTime = result.sendTime;
				that.msg = result.msg;
				that.senderName = result.senderName;
				uni.setNavigationBarTitle({
					title: that.senderName
				});
			});
		},
		onLoad(options) {
			let that = this;
			that.id = options.id;
			that.readFlag = options.readFlag == 'true' ? true : false;
			that.refId = options.refId;
			if (!that.readFlag) {
				that.ajax(that.url.updateUnreadMessage, "POST", {"id": that.refId}, function(resp){
					console.log("此消息已更新为已读状态");
				})
			}
		},
		methods: {
			deleteMsg: function() {
				let that = this;
				uni.showModal({
					title: "提示信息",
					content: "是否要删除这条消息？",
					success: function(resp) {
						if (resp.confirm) {
							that.ajax(that.url.deleteMessageRefById, "POST", {"id": that.refId}, function(resp){
								uni.showToast({
									icon: "success",
									title: "删除成功",
									complete: function() {
										setTimeout(() => {
											uni.navigateBack({
												delta: 1
											});
										}, 1000);
									}
								})
							})
						}
					}					
				})
			}
		}
	}
</script>

<style lang="less">
	@import url("message.less");
</style>
